utton\" @click=\"postArticle\">发射!</button>\n            <button class=\"am-fr am-btn am-round am-btn-secondary\" type=\"button\" @click=\"delete\">删除</button>\n        </div>\n        <div id=\"show-content\" class=\"am-u-sm-6\">\n            {{{editorContent | marked}}}\n        </div>\n    </article>\n</template>\n<style scoped>\n\n    section {\n        background: #fff;\n        padding:15px 20px 10px;\n        margin:20px 0;\n        border-radius: 5px;\n        box-shadow: 0 0 5px #f7f7f7;\n    }\n    section > p {\n        font-size:14px;\n        color: #666;\n    }\n    textarea {\n        width:100%;\n        min-height:400px;\n        border:0;\n    }\n    h3 {\n        font-weight: bold;\n        font-size:18px;\n        margin:0 0 10px;\n    }\n    #editor-container button {\n        /*margin:20px 0;*/\n    }\n    #editor-container li {\n        margin:5px 10px;\n        cursor: pointer;\n    }\n    #editor-container li.active {\n        background: #3bb4f2;\n    }\n</style>\n<script>\n    import marked from 'marked';\n    import prism from 'prismjs';\n    let badge = [{\n        text : \"css\",\n        active : false\n    },{\n        text : \"js\",\n        active : false\n    },{\n        text : \"自动化构建\",\n        active : false\n    },{\n        text : \"自动化构建\",\n        active : false\n    },{\n        text : \"自动化构建\",\n        active : false\n    },{\n        text : \"自动化构建\",\n        active : false\n    },{\n        text : \"自动化构建\",\n        active : false\n    }];\n\n    export default{\n        data(){\n            return{\n                editorContent: '',\n                title : '',\n                badge : badge\n            }\n        },\n        route: {\n            data() {\n                if(!this.$route.params.id) return;\n                this.$http.get('/kodo/article/detail/id/' + this.$route.params.id)\n                    .then(function(ret) {\n                        this.title = ret.data.title;\n                        this.editorContent = decodeURIComponent(ret.data.content);\n                    });\n\n            }\n        },\n        filters: {\n            marked: marked\n        },\n        ready() {\n            setTimeout(function() {\n                prism.highlightAll(false);\n            },50)\n        },\n        methods : {\n            postArticle() {\n                //TODO ajax 提交文章\n                let badges = [];\n                this.badge.forEach(function(item) {\n                    if(item.active)\n                        badges.push(item.text);\n                });\n                if(this.$route.params.id) {\n                    update(this,this.$route.params.id)\n                } else {\n                    create(this);\n                }\n\n            },\n            markRender () {//实时渲染markdown\n                prism.highlightAll(false);\n            },\n            choiceBadge(idx){\n                this.badge[idx].active ? this.badge[idx].active = false : this.badge[idx].active = true;\n            },\n            delete() {\n                if(!confirm('确认删除吗?')) return;\n                this.$http.post(\"/kodo/article/delete\",{\n                    id : this.$route.params.id\n                }).then(function(ret) {\n                    alert('更新成功');\n                })\n            }\n        },\n        components:{\n            \n        }\n    }\n    function update(v,id) {\n        v.$http.post(\"/kodo/article/update\",{\n            id : id,\n            title : v.title,\n            content: v.editorContent\n        }).then(function(ret) {\n            alert('更新成功');\n        })\n    }\n    function create(v) {\n        v.$http.post(\"/kodo/article/add\",{\n            title : v.title,\n            content: v.editorContent,\n            bg : JSON.stringify(v.badges)\n        }).then(function(ret) {\n            alert('发射成功');\n        })\n    }\n</script>"],"sourceRoot":"webpack://"}]);

